import React, { useState } from "react";
// import IconTutorial from "../tutorial/IconTutorial";
// import CheckBoxTutorial from "../tutorial/CheckBoxTutorial";
// import SwitchTutorial from "../tutorial/SwitchTutorial";
import clsx from "clsx";
import {
  Grid,
  AppBar,
  Tabs,
  Tab,
  Typography,
  Box,
  makeStyles
} from "@material-ui/core";

export const useLoopAnimation = sec => {
  const [value, setValue] = useState(false);

  React.useEffect(() => {
    setInterval(() => {
      setValue(x => !x);
    }, sec * 1000);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return value;
};

const useStyles = makeStyles(theme => ({
  test: {
    border: "solid 1px",
    margin: "100px auto",
    width: "450px",
    height: "450px",
    transition: "width 1s, height 1s, background-color 1s, transform 1s"
  },
  test1: {
    transform: "scale(0.9)",
    transition: "width 1s, height 1s, background-color 1s, transform 1s"
  }
}));

const Tutorial = () => {
  const classes = useStyles();
  const isOn = useLoopAnimation(1);

  return (
    <div
      // style={{
      //   border: "solid 1px"
      // }}
      // className={clsx(classes.test, {
      //   [classes.test1]: value
      // })}
      className={clsx(classes.test, isOn && classes.test1)}
    >
      <h1>Tutorial</h1>
      <button>ttt</button>
    </div>
  );
};

export default Tutorial;
